import { USER_API } from "@/api/client";
import { mapState } from "vuex";
// import activity from "@/components/activity";
import pupProtocol from "./protocol.vue";
import trading from "@/components/trading";
export default {
  name: "trading",
  data() {
    return {
      status: false,
      show: false,
      isLoading: true,
      isLoadingList: false,
      refreshLoading: false,
      loading: false, //上拉加载
      finished: false, //上拉加载完毕
      start: 1,
      activeType: 0,
      theme: 0,
      waitClaim: 0,
      activeList: [
        this.$t("trading.Live"),
        this.$t("trading.Upcoming"),
        this.$t("trading.Past"),
      ],
      tradingList: [],
    };
  },
  computed: {
    ...mapState(["local"]),
    locale() {
      return this.local.locale;
    },
    firstState() {
      return this.local.firstStatus;
    },
    isBitKeep() {
      return this.local.UA.isBitKeep;
    },
  },
  components: {
    // activityCom: activity,
    pupProtocol,
    tradingCom: trading,
  },

  mounted() {
    this.firstEnter();
    this.getInfo();
    this.setIcon();
    this.tradingWaitClaim();
  },
  methods: {
    firstEnter() {
      let tit =
        "<div class='popBox'><div>" +
        this.$t("trading.tit1") +
        "</div><div>" +
        this.$t("trading.tit2") +
        "</div></div>";
      if (this.firstState) {
        this.$dialog.alert({
          title: this.$t("trading.popTit"),
          message: tit,
          confirmButtonText: this.$t("trading.btn"),
          getContainer: () => document.querySelector("body"),
        });
        this.$store.commit('SET_firstStatus', false);
      } else {
        return;
      }
    },
    // 获取列表
    async getInfo() {
      if(!this.refreshLoading) this.isLoadingList = true;
      const { data, status } = await USER_API.tradingGetList({
        start: this.start,
        limit: 20,
        type: this.activeType + 1
      });
      if (status == 1) {
        this.isLoading = false;
        return this.$dialog.alert({
          message: data,
          confirmButtonText: this.$t("staking.know"),
          confirmButtonColor: "$theme-light-colorPrimary",
        });
      }
      var moreList = data.list;
      this.finished = false;
      if(this.start == 1 ){
        this.tradingList = [];
        this.tradingList = moreList;
      }else{
        this.tradingList.push(...moreList);
      }
      this.refreshLoading = false;
      this.loading = false;
      this.isLoading = false;
      this.isLoadingList = false;
      this.$toast.clear();
      if (this.tradingList.length >= data.totalRows) {
        this.finished = true;
      }
    },
    // 上拉加载请求方法
    onLoadList() {
      this.start++;
      this.finished = false;
      this.getInfo();
    },
    onDownRefresh() {
      this.start = 1;
      this.getInfo();
    },
    async tradingWaitClaim(){
      const { data, status } = await USER_API.tradingWaitClaim();
      if (status == 1) {
        return this.$dialog.alert({
          message: data,
          confirmButtonText: this.$t("staking.know"),
          confirmButtonColor: "$theme-light-colorPrimary",
        });
      }
      this.waitClaim = data;
    },
    tabChange(val) {
      this.activeType = val;
      this.getInfo();
    },
    setIcon() {
      this.isBitKeep &&
        BitKeepInvoke.onLoadReady(() => {
          BitKeepInvoke.setTitle(this.$t("trading.tradingTitle"));
          //设置主题
          this.$nextTick(() => {
            BitKeepInvoke.appMode((err, res) => {
              let body = document.getElementsByTagName("body")[0];
              if (res == 1) {
                this.theme = 1;
                body.setAttribute("class", "theme-dark");
                setTimeout(() => {
                  BitKeepInvoke.setIconAction(
                    "https://cdn.bitkeep.vip/u_b_09035ca0-4dd9-11ec-a555-07d5354e6fab.png",
                    () => {
                      this.$router.push("/activity/trading/history");
                    }
                  );
                }, 500);
              } else {
                this.theme = 0;
                body.setAttribute("class", "theme-light");
                setTimeout(() => {
                  BitKeepInvoke.setIconAction(
                    "https://cdn.bitkeep.vip/u_b_2bb4fa20-3b86-11ec-8e63-1db435df936c.png",
                    () => {
                      this.$router.push("/activity/trading/history");
                    }
                  );
                }, 500);
              }
            });
          });
        });
    },
    milliFormat(num) {
      return (
        num &&
        num
          .toString()
          .replace(/^\d+/g, (m) => m.replace(/(?=(?!^)(\d{3})+$)/g, ","))
      );
    },
    claim() {
      this.show = true;
    },
    close(val) {
      if (val) {
        setTimeout(() => {
          this.tradingWaitClaim();
        }, 200);
      }
      this.show = false;
    },
    swap(swapNow) {
      // let swap = {
      //   symbol0: {
      //     symbol: "CAKE",
      //     chain: "CAKE",
      //     chainName: "CAKE",
      //     contract: "contract",
      //     icon: "123",
      //   },
      //   symbol1: {
      //     symbol: "BNB",
      //     chainName: "BNB",
      //     chain: "BNB",
      //     contract: "contract",
      //     icon: "123",
      //   },
      // }
      BitKeepInvoke.nativeApp(JSON.stringify(swapNow));
    },
    learnMore() {
      this.$router.push("/activity/trading/tradingRule");
    },
  },
};
